body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://wallpapertag.com/wallpaper/middle/5/1/a/124353-popular-gym-background-1920x1110-for-android-tablet.jpg#.X7wAlxCFb5I.link);
  background-repeat: no-repeat;
  background-size: 2000px 1400px;
  color: white;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.6) 0%,rgba(0,0,0,0.6) 100%), url(https://wallpapertag.com/wallpaper/middle/5/1/a/124353-popular-gym-background-1920x1110-for-android-tablet.jpg#.X7wAlxCFb5I.link);

}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h3 {
  color: white;
}

.table td {
  color: white;
  background-color: rgba(53, 46, 46, 0.39);
}


.navbar {
  height: 120px;
}

label {
  color: white;
}

a.nav-link {
  font-size: 18pt;
  text-shadow: 2px 2px grey;
  align-items: right;
}

a.navbar-brand {
  font-size: 32pt;
  text-shadow: 2px 2px black;

}

.navbar-dark .navbar-nav .nav-link {
  color: white;
}

a {
  color: white!important;
  
}

.navbar-nav {
  text-align: right!important;
}